.List {
	display: flex;
	justify-content: center;
	user-select: none;
	position: relative;
	transition: all 0.3s ease 0s;
	&__container {
		width: 500px;
		height: auto;
		box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.103);
		background-color: white;
		border-top: rgb(255, 232, 232) solid 1px;
	}
	&__list {
		width: 100%;
		height: auto;
		padding: 5px;
	}
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 10px;
		cursor: pointer;
	}

	&__checkbox {
		display: block;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		border: rgb(0, 0, 0) solid 1px;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	&__completed {
		color: rgb(164, 226, 255);
		font-size: 24px;
	}
	&__todoText {
		flex: 1 1 auto;
		font-size: 24px;
		padding-left: 15px;
		font-weight: 400;
		transition: all 0.3s ease 0s;
	}
	&__todoText.completedText {
		color: rgb(209, 209, 209);
		text-decoration: line-through;
	}
	&__itemDel {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 24px;
		width: 30px;
		height: 30px;
	}

}

.open {
	top: 0px;
	opacity: 1;
	pointer-events: auto;
}
.close {
	pointer-events: none;
	opacity: 0;
	top: -40px;
}