.Form {
	display: flex;
	justify-content: center;
	align-items: center;
	&__container {
		height: 50px;
		padding: 20px;
		width: 500px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: white;
		box-shadow: -10px 0px 20px 1px rgba(0, 0, 0, 0.103);
		position: relative;
		z-index: 10;
	}
	&__input {
		flex: 1 1 auto;
		font-size: 24px;
		padding: 5px 15px;
		&::placeholder {
			color: rgb(209, 209, 209);
		}
	}
	&__btnAdd,
	&__btnOpen {
		cursor: pointer;
		font-size: 24px;
		width: 24px;
		height: 24px;
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: white;
		transition: all 0.3s ease 0s;
	}
	.open {
		transform: rotate(90deg);
	}
	
}

.spin {
	animation: addTodo 2s ease;
}

@keyframes addTodo {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(720deg);
	}
}